@keyframes blackLine {
  0% {
    width: 0%;
    left: 0;
  }
  50% {
    width: 100%;
    left: 0;
  }
  100% {
    width: 0;
    left: 100%;
  }
}
@keyframes rotating {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes dash {
  to {
    stroke-dashoffset: 0;
  }
}
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  font-size: 62.5%;
}
@media screen and (max-width: 1250px) {
  html {
    font-size: 52%;
  }
}

body {
  font-family: "Montserrat", sans-serif;
  max-width: 100vw;
  background-color: #353535;
  overflow-x: hidden;
  color: #161616;
}

header {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

main {
  margin: 0 auto;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

a {
  text-decoration: none;
  color: inherit;
}

p::selection,
h1::selection,
h2::selection,
h3::selection,
h4::selection,
a::selection,
div::selection {
  background: #c7a780;
  color: #161616;
}

h2 {
  font-family: "Montserrat", sans-serif;
  font-size: 16rem;
}

h3 {
  font-family: "Montserrat", sans-serif;
  font-size: 4.8rem;
  font-weight: 800;
  letter-spacing: 1.2rem;
}

#smooth-scrollbar {
  overflow: hidden;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.nav {
  padding: 6rem 0;
  font-size: 2rem;
  font-weight: 300;
  color: #c0c0c0;
  max-width: 120rem;
  margin: 0 auto 0 auto;
  font-family: "Playfair Display", serif;
}
@media screen and (max-width: 600px) {
  .nav {
    padding: 6rem 0rem 6rem 0rem;
  }
}
.nav__list {
  list-style: none;
  display: flex;
  justify-content: flex-end;
}
@media screen and (max-width: 900px) {
  .nav__list {
    justify-content: center;
  }
}
.nav__item {
  cursor: pointer;
  position: relative;
  text-transform: lowercase;
  font-size: 1.8rem;
  font-weight: 600;
  color: #c7a780;
}
.nav__item::before {
  content: "";
  position: absolute;
  top: 50%;
  width: 0;
  height: 140%;
  background-color: #161616;
  transform: translateY(-50%);
  z-index: -1;
}
.nav__item:hover::before {
  animation-name: blackLine;
  animation-fill-mode: forwards;
  animation-duration: 0.8s;
}
.nav__item:nth-child(1) {
  margin-right: 10rem;
}

.side-dot-nav {
  position: fixed;
  top: 50%;
  left: 0;
  transform: translate(-100%, -50%);
  color: #c0c0c0;
  font-size: 1.5rem;
  padding-left: 2rem;
  z-index: 999;
  visibility: hidden;
}
@media screen and (max-width: 900px) {
  .side-dot-nav {
    display: none;
  }
}
.side-dot-nav__item {
  padding: 0.8rem;
  list-style: none;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.side-dot-nav__item--dot {
  height: 6px;
  width: 6px;
  background-color: rgba(192, 192, 192, 0.5);
  border-radius: 50%;
  margin-right: 2rem;
  position: relative;
  box-shadow: 0px 0px 25px 2px rgba(22, 22, 22, 0.6);
}
.side-dot-nav__item--dot::before {
  content: "";
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
  width: 100%;
  border-radius: 50%;
  border: 1px solid rgba(192, 192, 192, 0.8);
  position: relative;
  transition: all 0.3s linear;
}
@media (hover: hover) {
  .side-dot-nav__item:hover .side-dot-nav__item--dot::before {
    height: 400%;
    width: 400%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .side-dot-nav__item:hover .side-dot-nav__item--label {
    opacity: 1;
    transform: translateX(0px);
  }
}
.side-dot-nav__item--active .side-dot-nav__item--dot::before {
  height: 400%;
  width: 400%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.side-dot-nav__item--active .side-dot-nav__item--label {
  opacity: 1;
  transform: translateX(0px);
}
.side-dot-nav__item--label {
  opacity: 0;
  transform: translateX(10px);
  transition: all 0.3s linear;
  text-shadow: 0 0 5px rgba(22, 22, 22, 0.6);
}

.hero {
  display: flex;
  justify-content: center;
  min-height: 75vh;
  max-width: 120rem;
  margin: 0 auto;
  margin-top: 6rem;
  color: #161616;
}
@media screen and (max-width: 600px) {
  .hero {
    margin-top: 4rem;
    margin-bottom: 4rem;
    min-height: 0;
  }
}
.hero__text {
  font-size: 7.4rem;
  font-weight: 600;
}
@media (max-width: 600px) {
  .hero__text {
    font-size: 6rem;
  }
}
@media (max-width: 400px) {
  .hero__text {
    font-size: 4.5rem;
  }
}
.hero__text p {
  margin-bottom: 2rem;
}
.hero__link {
  font-size: 1.8rem;
  color: #c7a780;
  position: relative;
  display: inline-block;
  font-family: "Playfair Display", serif;
}
.hero__link:hover a {
  transform: translateX(2.6rem);
}
.hero__link:hover::before {
  width: 2rem;
}
.hero__link a {
  display: block;
  transition: transform 0.3s ease-out;
}
.hero__link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: 0rem;
  height: 1px;
  background-color: #c7a780;
  transition: width 0.3s ease-out;
}

.contact {
  min-height: 50vh;
  position: relative;
  max-width: 120rem;
  margin: 0 auto;
}
.contact__header {
  color: rgba(192, 192, 192, 0.1);
  margin-bottom: 4rem;
}
@media screen and (max-width: 900px) {
  .contact__header {
    font-size: 12rem;
  }
}
@media screen and (max-width: 600px) {
  .contact__header {
    font-size: 8rem;
  }
}
.contact h3 {
  margin-bottom: 1.25rem;
}
.contact-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8rem;
}
@media screen and (max-width: 600px) {
  .contact-wrapper {
    grid-template-columns: 1fr;
    gap: 0rem;
  }
}
.contact__card {
  width: 100%;
}
@media screen and (max-width: 600px) {
  .contact__card {
    margin-bottom: 2rem;
  }
}
.contact__card p {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  color: #c0c0c0;
}
.contact__card p a {
  display: flex;
  align-items: center;
}
.contact__card p svg {
  margin-right: 1.8rem;
}
.contact__card .contact__icons {
  color: #c0c0c0;
}

.portfolio {
  min-height: 100vh;
  margin-bottom: 15rem;
}
@media screen and (max-width: 900px) {
  .portfolio {
    margin-bottom: 10rem;
  }
}
.portfolio__header {
  text-align: right;
  max-width: 120rem;
  margin: 0 auto;
  color: rgba(22, 22, 22, 0.5);
  margin-bottom: 5rem;
}
@media screen and (max-width: 900px) {
  .portfolio__header {
    font-size: 12rem;
  }
}
@media screen and (max-width: 600px) {
  .portfolio__header {
    font-size: 8rem;
  }
}

.portfolio__grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(290px, 1fr));
  gap: 1.5rem;
  max-width: 1890px;
  margin: 0 auto;
}
@media (max-width: 680px) {
  .portfolio__grid {
    grid-template-columns: 1fr;
  }
}

.portfolio__item {
  display: block;
  position: relative;
  overflow: hidden;
}
.portfolio__item:last-child {
  grid-row: 2/3;
  grid-column: 1/2;
}
@media (max-width: 600px) {
  .portfolio__item:last-child {
    display: none;
  }
}

.portfolio__image {
  display: block;
  height: 100%;
  width: 100%;
  overflow: hidden;
  pointer-events: none;
}
.portfolio__image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transform: scale(1.05);
  transition: transform 0.4s ease-out;
  pointer-events: none;
}

.portfolio__overlay {
  background-color: rgba(22, 22, 22, 0.2);
  position: absolute;
  inset: 0;
  z-index: 4;
  transition: opacity 0.2s ease-out;
  color: #c7a780;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.8rem;
  font-weight: 800;
  padding: max(10px, 5%);
  text-align: center;
  text-transform: uppercase;
}

.overview {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 101%;
  height: 101%;
  inset: 0;
  z-index: 10;
}
.overview__container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  background-color: #c7a780;
  transform: translate(-50%, -50%);
  padding: 5%;
}
.overview h3 {
  text-transform: uppercase;
  text-align: right;
  font-size: 3.8rem;
}
@media (max-width: 980px) {
  .overview h3 {
    font-size: 2.8rem;
  }
}
@media (max-width: 680px) {
  .overview h3 {
    font-size: 3.8rem;
  }
}
@media (max-width: 480px) {
  .overview h3 {
    font-size: 2.2rem;
  }
}
.overview__link {
  display: block;
  text-align: right;
}
@media (max-width: 980px) {
  .overview__link svg {
    height: 35px;
    width: 35px;
  }
}

.cover {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  inset: 0;
  z-index: 9;
  font-size: 2rem;
}

.portfolio__item:nth-child(even) .overview__container {
  background-color: #161616;
  color: #c0c0c0;
}
.portfolio__item:nth-child(even) .cover {
  background-color: #c7a780;
}
.portfolio__item:nth-child(even) .text-outline {
  -webkit-text-fill-color: #161616;
  /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: #c0c0c0;
}
@media (max-width: 480px) {
  .portfolio__item:nth-child(even) .text-outline {
    -webkit-text-stroke-width: 1px;
  }
}

.portfolio__item:nth-child(odd) .overview__container {
  background-color: #c7a780;
}
.portfolio__item:nth-child(odd) .cover {
  background-color: #161616;
  color: #c7a780;
}
.portfolio__item:nth-child(odd) .text-outline {
  -webkit-text-fill-color: #c7a780;
  /* Will override color (regardless of order) */
  -webkit-text-stroke-width: 2px;
  -webkit-text-stroke-color: black;
}
@media (max-width: 480px) {
  .portfolio__item:nth-child(odd) .text-outline {
    -webkit-text-stroke-width: 1px;
  }
}

.underline {
  position: relative;
  display: inline-block;
}
.underline:hover::after {
  height: 100%;
  bottom: 0;
}
.underline::after {
  content: "";
  width: 100%;
  height: 50%;
  background-color: #c7a780;
  position: absolute;
  bottom: 0rem;
  left: 0rem;
  z-index: -1;
  transition: all 0.2s ease-out;
}
@media (max-width: 480px) {
  .underline::after {
    content: none;
    display: none;
  }
}
.underline::before {
  content: "";
  height: 100%;
  background-color: #161616;
  position: absolute;
  bottom: 0rem;
  left: 0rem;
  z-index: 2;
  animation-name: blackLine;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-iteration-count: 1;
}
@media (max-width: 480px) {
  .underline::before {
    content: none;
    display: none;
  }
}
.underline:nth-of-type(2)::before {
  animation-delay: 0.2s;
}
.underline:nth-of-type(3)::before {
  animation-delay: 0.4s;
}

.text-serif {
  font-family: "Playfair Display", serif;
}

.enlarge {
  transition: transform 0.4s ease-out;
}
.enlarge:hover, .enlarge:focus-visible {
  transform: scale(1.1);
}

.lines {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  overflow: hidden;
  height: 100%;
}
.lines svg circle {
  stroke-dasharray: 6000;
  stroke-dashoffset: 6000;
  animation: dash 3s linear forwards;
}
.lines svg path {
  stroke-dasharray: 5000;
  stroke-dashoffset: 5000;
  animation: dash 3s ease 2s forwards;
}
@media (max-width: 600px) {
  .lines svg path {
    transform: translateX(-20%) scale(0.7);
  }
}

@media (max-width: 480px) {
  .no-break {
    display: none;
  }
}

@media (min-width: 481px) {
  .space {
    display: none;
  }
}

.form {
  padding-bottom: 4rem;
  text-align: left;
}
.form .wrapper {
  max-width: 26.25rem;
  margin: 0 auto;
}
.form .form-control {
  margin-bottom: 1.6rem;
  position: relative;
}
.form .form-control:before:focus-within {
  transform: scaleX(1);
}
.form__focus-line {
  width: 100%;
  height: auto;
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid #c7a780;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease;
}
.form input[type=text],
.form input[type=email] {
  background: rgba(22, 22, 22, 0.15);
  border: 0;
  height: 5rem;
  font-size: 1.6em;
  font-weight: 400;
  width: 100%;
  padding: 0 1.5rem;
  outline: none;
  color: #c0c0c0;
  font-family: "Montserrat", serif;
}
.form input[type=text]:focus ~ .form__focus-line,
.form input[type=email]:focus ~ .form__focus-line {
  transform: scaleX(1);
}
.form input[type=text]:focus + label,
.form input[type=email]:focus + label {
  transform: translateY(-3.2rem);
  font-size: 1.2rem;
  padding: 0 7px;
}
.form input[type=text]:not(:placeholder-shown) + label,
.form input[type=email]:not(:placeholder-shown) + label {
  transform: translateY(-3.2rem);
  font-size: 1.2rem;
  padding: 0 7px;
}
.form input[type=text]::placeholder,
.form input[type=email]::placeholder {
  opacity: 0;
  font-weight: normal;
  color: #161616;
}
.form input[type=text]:focus::placeholder,
.form input[type=email]:focus::placeholder {
  opacity: 1;
}
.form input[type=text].success,
.form input[type=email].success {
  border-bottom: 1px solid #00ff55;
  color: #00ff55;
}
.form input[type=text].fail,
.form input[type=email].fail {
  border-bottom: 1px solid #fd293d;
  color: #fd293d;
}
.form .form-control--success::after {
  content: url(/assets/images/iconmonstr-checkbox-21.png);
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 1.125rem;
}
.form .form-control--fail::after {
  content: "!";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 1.125rem;
  color: #fd293d;
}
.form input[type=text].success + label,
.form input[type=email].success + label {
  color: #00ff55;
}
.form input[type=text].fail + label,
.form input[type=email].fail + label {
  color: #fd293d;
}
.form label {
  font-size: 1.6rem;
  position: absolute;
  left: 1.5rem;
  top: 50%;
  transform: translateY(-55%);
  transition: all 0.1s ease-out;
  pointer-events: none;
  color: #afafaf;
}
.form input[type=submit] {
  background-color: transparent;
  height: 5rem;
  border: 1px solid #c7a780;
  color: #c7a780;
  font-weight: 400;
  font-size: 1.6rem;
  font-family: inherit;
  cursor: pointer;
  width: 100%;
  text-transform: uppercase;
  transition: all 0.3s ease-out;
}
.form input[type=submit]:hover {
  background-color: #c7a780;
  color: #161616;
}
.form textarea {
  background: rgba(22, 22, 22, 0.15);
  border: 0;
  font-size: 1.6em;
  font-weight: 400;
  width: 100%;
  padding: 1.5rem;
  outline: none;
  color: #c0c0c0;
  font-family: "Montserrat", serif;
}
.form textarea ~ label {
  top: 1.5rem;
  transform: translateY(0%);
}
.form textarea:focus ~ .form__focus-line {
  transform: scaleX(1);
}
.form textarea:not(:placeholder-shown) + label {
  transform: translateY(-2.25rem);
  font-size: 1.2rem;
  padding: 0 7px;
}
.form textarea::placeholder {
  opacity: 0;
  font-weight: normal;
  color: #161616;
}
.form textarea:focus::placeholder {
  opacity: 1;
}
.form textarea:focus + label {
  transform: translateY(-2.25rem);
  font-size: 1.2rem;
  padding: 0 7px;
}
.form textarea.fail + label {
  color: #fd293d;
}
.form textarea.fail {
  border-bottom: 1px solid #fd293d;
  color: #fd293d;
}
.form textarea.fail:focus ~ .form__focus-line {
  border-bottom: 1px solid #fd293d;
}

.form__message p {
  padding: 1rem 0;
  font-size: 1.6rem;
  color: #c7a780;
}
.form__message.success p {
  color: #00ff55;
}
.form__message.fail p {
  color: #fd293d;
}